.footer-container{
    width: 100%;
    height: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.footer-information{
    width: 85%;
    height: 10rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #7d7d7d;
}
.items-container-footer{
    width: 100%;
    height: 5rem;
    padding:  0 15%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;

}
.items-container-footer-the-item{
    height:100%;
    display:flex ;
    flex-direction:row;
    align-items:center  ;
    justify-content: flex-start;
    margin: 1.5rem 0 ;
    cursor:pointer ;
    font-size: 1.3rem;
}
.footer-down{
    width: 100%;
    height: 1.4rem;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
}
.btn-footer-calss{
    padding: 1.2rem 2rem !important;
    font-size: 1.5rem !important;
    border-radius: 1rem !important;
}