.vendor-modal-container{
    min-width: 32rem;
    min-height: 14rem;
    max-height: 80vh;
    border-radius: 0.6rem;
    position: fixed;
    top: 10%;
    left: 35%;
    transition: 0.3s;
    box-shadow: 0px 7px 23px 0 #48484a;
    background-color: white;
    z-index: 300;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    /*padding: 1.4rem 2rem;*/
    overflow-y: scroll;
}
.vendor-modal-container::-webkit-scrollbar{
    width:0;
    height: 0;
}