.features-card{
    width: 32%;
    height: 13rem;
    background-color: white;
    border-radius: 0.7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.features-card-icon-container{
    width: 5rem;
    height: 5rem;
    border-radius: 0.7rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transform: translate(0 ,-1.5rem);
}