.client-says{
    width: 100%;
    height: 45rem;
    padding: 6rem 0 6rem 7.5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}
.client-says-describe-and-slider-container{
    width: 100%;
    padding-right: 7.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.clients-says-slider-container{
    width: 55%;
    padding-right: 7.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.clients-says-sliders {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 80%;
}

.client-comments-container{
    width: 100%;
    height: 29rem;
    padding-top: 3rem;
    overflow-x: auto;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    scroll-behavior:smooth;
    transition: 0.5s;


}
.client-comments-container::-webkit-scrollbar {
    display: none;
    width: 0px;
    height: 0px;
}

.client-comments-container ::-moz-scrollbar {
    display: none;
    width: 0px;
    height: 0px;

}
